@import url(https://fonts.googleapis.com/css?family=Viga&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #320b8d;
}
#drum-machine {
  display: flex;
  flex-direction: column;
}
.drum-h1 {
  font-family: "Viga", sans-serif;
  color: #ffc21c;
  font-size: 60px;
  text-shadow: -2px 2px #f72585;
  transition-duration: 0.5s;
  cursor: -webkit-grab;
  cursor: grab;
  margin: auto;
  margin-top: 100px;
  height: 150px;
}
.drum-h1:hover {
  text-shadow: 2px 2px #f72585;
}
@media only screen and (max-width: 768px) {
  .drum-h1 {
    font-size: 50px;
  }
}

#display {
  background: #ffc21c;
  border-radius: 5px;
  color: #ca236e;
  height: 15%;
  margin: 5px;
  font-family: "Orbitron", sans-serif;
  font-size: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: -1px 2px 1px 1px #f72585;
  transition-duration: 0.4s;
  cursor: pointer;
}
#display:hover {
  color: #000000;
}

.drum-container {
  border-radius: 5px;
  height: 400px;
  width: 350px;
  color: white;
  margin: auto;
  font-family: "Orbitron", sans-serif;
}
.drum-buttons-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.drum-pad {
  font-family: "Orbitron", sans-serif;
  width: 70px;
  height: 70px;
  background-color: rgb(255, 255, 255);
  color: #f72585;
  font-size: 40px;
  border-radius: 5px;
  box-shadow: 0px 5px 0px 0px #ffc21c;
  border: none;
  cursor: pointer;
  margin: 20px;
}
.drum-pad:active {
  color: #ffc21c;
  background-color: rgb(114, 112, 112);
  box-shadow: 0px 1px 0px 0px #ffc21c;
  transform: translateY(4px);
}
.drum-pad-pressed {
  color: #ffc21c;
  background-color: rgb(114, 112, 112);
  box-shadow: 0px 1px 0px 0px #ffc21c;
  transform: translateY(4px);
}

